import {Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import {DatePipe} from '@angular/common';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {ActivatedRoute, Router} from '@angular/router';
import {ToastrService} from 'ngx-toastr';
import {TranslateService} from '@ngx-translate/core';

import {CollectionsService} from '../../../../../core/services/collections.service';
import {ItemService} from '../../../../../core/services/item.service';
import {ClientService} from '../../../../../core/services/client.service';
import {TaxService} from '../../../../../core/services/tax.service';
import {AuthenticationService} from '../../../../../core/services/authentication.service';
import {NgOption} from '@ng-select/ng-select';
import {environment} from '../../../../../../environments/environment';
import * as Dropzone from 'dropzone';

@Component({
    selector: 'app-collection-edit',
    templateUrl: './collection-edit.component.html',
    styleUrls: ['./collection-edit.component.scss']
})

export class CollectionEditComponent implements OnInit {
    editCollectionForm: FormGroup;
    selectedItem: number;
    loginUser: any;
    urlPage:string;
    collection: any;
    moduletype: string;
    isLogoLoaded = true;
    listcollections = [];
    listTemplate: any = [];
    isFormSubmitted = false;
    isFormLoaded = false;
    liststatus: NgOption[] = [
        {value: 'no', label: 'Hide'},
        {value: 'yes', label: 'Show'},
    ];
    datepickerConfig = {
        dateInputFormat: 'YYYY-MM-DD',
        containerClass: 'theme-red'
    };
    @ViewChild('logodropzone', {static: false}) logodropzone: ElementRef;
    private apiUrl = environment.apiUrl;
    constructor(
        public translate: TranslateService,
        public datepipe: DatePipe,
        private router: Router,
        private route: ActivatedRoute,
        private formBuilder: FormBuilder,
        private toastr: ToastrService,
        private collectionService: CollectionsService,
        private clientService: ClientService,
        private itemService: ItemService,
        private taxService: TaxService,
        private authenticationService: AuthenticationService
    ) {
        this.authenticationService.loginUser.subscribe(x => this.loginUser = x);
        this.route.paramMap.subscribe(params => {
            this.getById(params.get('id'));
        });
    }

    get collectionControl() {
        return this.editCollectionForm.controls;
    }

    ngOnInit() {
        if (this.router.url.indexOf('/productcollections/edit') !== -1) {
            this.moduletype = 'products';
            this.urlPage = '/productcollections';
        } else {
            this.moduletype = 'news';
            this.urlPage = '/newscollections';
        }
    }

    loadForm() {
        let that = this;
        this.editCollectionForm = this.formBuilder.group({
            id: [this.collection.id, Validators.required],
            name: [this.collection.name, Validators.required],
            description: [this.collection.description],
            page_title: [this.collection.page_title],
            page_description: [this.collection.page_description],
            url_and_handle: [this.collection.url_and_handle, Validators.required],
            active: [this.collection.active, Validators.required],
            avatar: [this.collection.avatar, Validators.required],
            keyword: [this.collection.keyword],
            po_types_indentify:[this.moduletype],
            pages_id: [this.collection.pages_id, Validators.required],
        });
        this.editCollectionForm.patchValue({avatar: this.collection.avatar});
        setTimeout(() => {
            that.loadDropzone();
        });
        this.isFormLoaded = true;
    }

    getById(collectionId) {
        this.collectionService.getById(collectionId).subscribe(data => {
            this.collection = data;
            this.getTemplate();
            this.getCollections();
        });
    }
    getCollections() {
        this.collectionService.getAll().subscribe(
            data => {
                this.listcollections = data;

                this.loadForm();
            });
    }

    getTemplate() {
        this.collectionService.getTemplate().subscribe(
            data => {
                this.listTemplate = data;
            });
    }
    onSubmit() {
        this.isFormSubmitted = true;
        if (this.editCollectionForm.invalid) {
            return;
        }

        this.collectionService.update(this.editCollectionForm.value)
            .subscribe(
                data => {
                    this.toastr.success(this.translate.instant('collections.messages.update'), this.translate.instant('collections.title'));
                    this.router.navigate([this.urlPage]);
                });
    }
    loadDropzone() {
        let that = this;
        new Dropzone(this.logodropzone.nativeElement, {
            url: 'https://httpbin.org/post',
            maxFiles: 1,
            clickable: true,
            acceptedFiles: 'image/*',
            createImageThumbnails: true,
            init: function () {
                this.on('addedfile', function (file) {
                    const removeButton = Dropzone.createElement('<button class=\'btn btn-sm btn-block\'>Remove file</button>');
                    const _this = this;
                    removeButton.addEventListener('click', function (e) {
                        e.preventDefault();
                        e.stopPropagation();
                        _this.removeFile(file);
                    });

                    file.previewElement.appendChild(removeButton);
                    if (file) {
                        let reader = new FileReader();
                        reader.onload = (e) => {
                            that.editCollectionForm.patchValue({avatar: reader.result});
                            this.isLogoLoaded = false;
                        };
                        reader.readAsDataURL(file);
                    }
                });

                this.on('removedfile', function (file) {
                    that.editCollectionForm.patchValue({avatar: null});
                });

                this.on('error', function (file, message: any) {
                    if (file) {
                        that.toastr.error(message);
                    }
                });
            }
        });
    }

    removeDropzoneImage() {
        this.isLogoLoaded = false;
        this.editCollectionForm.patchValue({avatar: null});
    }
}
